<template>
    <div>
        <div class="logo">
            <img src="../assets/img/LOGO.png" alt="">
        </div>

        <div class="back">
            <i-button @click="$router.back(-1)">
                
            </i-button>
        </div>
        <h1></h1>
        <div class="table_box">
            <i-table id="table_box" stretch width="1800" height="500" :loading="loading" border :data="dataList" :columns="columnsList" stripe
                     size="small" ref="table">
            </i-table>
        </div>

        <!--<Button type="primary" size="large" @click="exportData(1)"><Icon type="ios-download-outline"></Icon></Button>-->

        <div class="btn_new">
            <i-button @click="addData" id="btn"></i-button>
            <!--<Button type="primary" size="large" @click="exportData(1)"><Icon type="ios-download-outline"></Icon></Button>-->
        </div>
        <Page :total="totalPage" :page-size="pageSize" :current="currentPage" :show-total="true" @on-change="pageChange"/>
    </div>

</template>

<script>

    import {bidData} from '../assets/js/datamsg'

    import layoutFn from '../assets/js/layout'
    import {publicData} from '../assets/js/common'

    import axios from 'axios'

    export default {
        name: "winbids",
        data: function () {
            return {
                loading: true,
                totalPage: 1,
                pageSize:10,
                currentPage:1,
                //
                dataList: [],
                /**
                 * @name columnsList ( )
                 * @author
                 * @see https://www.iviewui.com/components/table
                 * @param
                 * {
                 *  titleHtml : html : '<em class="blue" style="color:red"></em>'
                 *  editable  : true, 
                 *  option    : 
                 *  date      : data ,:
                 *              date | daterange: yyyy-MM-dd ()
                 *              datetime | datetimerange: yyyy-MM-dd HH:mm:ss
                 *              year: yyyy
                 *              month: yyyy-MM
                 *  input     : input ,html5 ( textarea ), text
                 *  handle    : , , 'edit', 'delete'
                 * }
                 * @version 0.0.1
                 */
                columnsList:bidData,
                // , ,  
                cloneDataList: [],
                success:false
            }
        },
        created() {

            // 
            const that = this;
            axios.all([
                that.$axios.get('/invariable_configs'),
                that.$axios.get('/variable_configs'),
                that.$axios.get('/win_bids')
            ]).then(axios.spread(function (invariable, variable,channel) {

                // 
                // console.log('invariable---', invariable.data);
                // console.log('variable---', variable.data);
                // console.log('channel---', channel.data.data);
                // console.log(channel)

                // 
                /*that.totalPage = channel.data.total;
                that.currentPage = channel.data.current_page;
                that.pageSize = channel.data.per_page;*/

                //
                //
                //
                that.columnsList[11].option = invariable.data.win_bid_unit;
                //
                // that.columnsList[4].option = invariable.data.record_aptitude;
                //
                that.columnsList[9].option = invariable.data.project_type;
                //
                // that.columnsList[12].option = invariable.data.is_set_filiale;
                //
                that.columnsList[15].option = invariable.data.is_end;
                //
                that.columnsList[16].option = invariable.data.is_up_to_par;
                //
                that.columnsList[8].option = invariable.data.contract_date_remark;
                //
                that.columnsList[17].option = invariable.data.achievement_unit;

                //
                //
                that.columnsList[10].option = variable.data.project_manager;
                // 
                that.columnsList[12].option = variable.data.business_channel;
                // /
                that.columnsList[13].option = variable.data.partner;
                //
                // that.columnsList[8].option = variable.data.record_legal_person;
                //
                // that.columnsList[10].option = variable.data.bond_submit_person;

                //

                that.listContent(channel);

                //
                // that.getData();
                that.init();

                // console.log(that.dataList)

            }));
        },
        mounted() {

        },
        methods: {
            /*getData() {
                // var that = this;
                // that.loading = true;
                //   
                // that.cloneDataList = JSON.parse(JSON.stringify(that.dataList)).map(function (item) {
                /!*that.cloneDataList = that.dataList.map(function (item) {
                    item.editting = false;
                    item.saving = false;
                    return item;
                });*!/

                // that.loading = false;
            },*/
            //
            init() {
                var that = this;

                this.columnsList.forEach(function (item) {
                    layoutFn(item,that)
                });

                this.loading = false;

            },
            saveData: function (currentRow, index,func) {
                var that = this;
                that.success = false;

                // that.loading = true;
                // , 
                // this.$set(this.dataList, index, this.handleBackdata(currentRow))
                // 
                // currentRow = Object.assign(currentRow,publicData)
                
                // console.log(publicData)

                let datas = {
                    area : publicData.area,
                    project_name : publicData.project_name,
                    win_bid_unit : publicData.win_bid_unit,
                    win_bid_price : publicData.win_bid_price,
                    contract_sign_at : publicData.contract_sign_at,
                    work_days : publicData.work_days,
                    begin_at : (function () {
                        if (publicData.begin_end == ''){
                            return ''
                        } else if (publicData.begin_end) {
                            return publicData.begin_end.split('')[0]
                        }
                    })(),
                    end_at : (function () {
                        if (publicData.begin_end == ''){
                            return ''
                        } else if (publicData.begin_end){
                            return publicData.begin_end.split('')[1]
                        }
                    })(),
                    project_index : publicData.project_index,
                    project_type : publicData.project_type,
                    project_manager : publicData.project_manager,
                    business_channel : publicData.business_channel,
                    contract_date_remark: publicData.contract_date_remark,
                    partner : publicData.partner,
                    win_bid_publicity : publicData.win_bid_publicity,
                    is_end : publicData.is_end,
                    is_up_to_par : publicData.is_up_to_par,
                    achievement_unit: publicData.achievement_unit,
                    remark : publicData.remark,
                }
                for (let key in datas) {
                    if(key != 'begin_at' && key != 'end_at'&& key != 'contract_date_remark'&& key != 'business_channel'&& key != 'remark'){
                        if (datas[key] == ''){
                            that.$Message.warning({
                                top: 100,
                                content:'',
                                duration:3,
                                onClose:function () {
                                    that.loading = false;
                                    currentRow.saving = false;
                                }
                            })
                            return false;
                        }else if(key == 'win_bid_publicity'&&datas[key] != ''){
                            //url
                            function isURL(str_url) {// url
                                var strRegex = "^((https|http|ftp|rtsp|mms)?://)"
                                    + "?(([0-9a-z_!~*'().&=+$%-]+: )?[0-9a-z_!~*'().&=+$%-]+@)?" // ftpuser@
                                    + "(([0-9]{1,3}\.){3}[0-9]{1,3}" // IPURL- 199.194.52.184
                                    + "|" // IPDOMAIN
                                    + "([0-9a-z_!~*'()-]+\.)*" // - www.
                                    + "([0-9a-z][0-9a-z-]{0,61})?[0-9a-z]\." // 
                                    + "[a-z]{2,6})" // first level domain- .com or .museum
                                    + "(:[0-9]{1,4})?" // - :80
                                    + "((/?)|" // a slash isn't required if there is no file name
                                    + "(/[0-9a-z_!~*'().;?:@&=+$,%#-]+)+/?)$";
                                var re = new RegExp(strRegex);
                                return re.test(str_url);
                            }
                            if (!isURL(datas[key])) {
                                that.$Message.warning({
                                    top: 100,
                                    content:'url',
                                    duration:3,
                                    onClose:function () {
                                        that.loading = false;
                                        currentRow.saving = false;
                                    }
                                })
                                return false;
                            }
                        }else if (key == 'win_bid_publicity'&&datas[key] == '') {
                            datas[key] = '';
                        }
                    }
                }

                //id
                if(currentRow.id){
                    this.$axios.put('win_bids',{...{id:currentRow.id},...datas}).then(function (res) {
                        that.loading = false;
                        currentRow.saving = false;

                        if(res.data.status == 0){
                            // currentRow.editting = false;
                            that.$Message.success(res.data.msg);
                            that.variable();
                            that.pageChange(1)
                            that.success = true;
                        }else{
                            // currentRow.saving = false;
                            // currentRow.editting = true;
                            that.$Message.warning(res.data.msg);

                        }

                        func()

                    })
                }else{
                    this.$axios.post('win_bids',datas).then(function (res) {
                        that.loading = false;

                        if (res.data.status == 0){
                            that.$Message.success(res.data.msg);
                            that.pageChange(1)
                            that.success = true;
                        } else{
                            currentRow.saving = false;
                            currentRow.editting = true;
                            that.$Message.warning(res.data.msg);
                        }
                        func()

                    })

                }

            },
            // 
            addData() {
                var that = this;
                that.loading = true;
                that.success = false;
                that.$Message.loading('');

                /*let newData = {
                    'area' : '/',
                    'project_name' : '',
                    'win_bid_unit' : '',
                    'win_bid_price' : '8000',
                    'contract_sign_at' : '2019-01-10',
                    'work_days' : '3',
                    // 'begin_at' : '2019-01-10',
                    // 'end_at' : '2022-01-10',
                    'begin_end':'2000-01-102022-01-10',
                    'contract_date_remark':'',
                    'project_index' : '',
                    'project_type' : ['',''],
                    'project_manager' : '',
                    'business_channel' : '',
                    'partner' : '',
                    'win_bid_publicity' : 'https://www.hao123.com/',
                    'is_end' : '',
                    'is_up_to_par' : '',
                    'achievement_unit':'',
                    'remark' : '666',
                    'editting' : true,
                    'saving' : false,
                };*/

                let newData = {
                    'area' : '',
                    'project_name' : '',
                    'win_bid_unit' : '',
                    'win_bid_price' : '',
                    'contract_sign_at' : '',
                    'work_days' : '',
                    // 'begin_at' : '',
                    // 'end_at' : '',
                    'begin_end':'',
                    'contract_date_remark':'',
                    'project_index' : '',
                    'project_type' : '',
                    'project_manager' : '',
                    'business_channel' : '',
                    'partner' : '',
                    'win_bid_publicity' : '',
                    'is_end' : '',
                    'is_up_to_par' : '',
                    'achievement_unit':'',
                    'remark' : '',
                    'editting' : true,
                    'saving' : false,
                };

                setTimeout(function () {
                    that.dataList.unshift(newData)
                    // that.cloneDataList.unshift(newData)
                    // console.log(that.dataList)
                    // console.log(that.cloneDataList)
                    // that.cloneDataList = JSON.parse(JSON.stringify(that.dataList)).map(function (item) {
                    /*that.cloneDataList = that.dataList.map(function (item) {
                        item.editting = true;
                        item.saving = false;
                        that.loading = false;

                        return item;
                    });*/
                    that.loading = false;
                    that.$Message.success('');
                }, 500)
            },
            // 
            deleteData(currentRow) {
                const that = this;
                that.loading = true;

                // console.log(currentRow.id);
                // console.log(that.dataList, index)

                this.$axios.delete('/win_bids?id='+currentRow.id).then(function (resolver) {
                    // console.log(resolver)

                    // that.$delete(that.dataList, index)
                    // that.$delete(that.cloneDataList, index)
                    that.$Message.success(resolver.data.msg);
                    that.loading = false;
                    if(that.dataList.length == 1){
                        that.pageChange(1)
                    }else{
                        that.pageChange(that.currentPage)
                    }
                })

               /* setTimeout(function () {
                    that.$delete(that.dataList, index)
                    that.$delete(that.cloneDataList, index)
                    that.$Message.success('');
                }, 1000)*/
            },
            // ,
            /*handleBackdata(object) {
                var clonedData = JSON.parse(JSON.stringify(object));
                // var clonedData = object;
                delete clonedData.editting;
                delete clonedData.saving;
                return clonedData;
            },*/
            //
            variable(){
                const that = this;
                this.$axios.get('/variable_configs').then(function (variable) {
                    //
                    //
                    that.columnsList[10].option = variable.data.project_manager;
                    // 
                    that.columnsList[12].option = variable.data.business_channel;
                    // /
                    that.columnsList[13].option = variable.data.partner;
                    
                })
            },
            //
            pageChange(value){
                this.loading = true;
                const that = this;
                this.$axios.get('/win_bids?page='+value).then(function (resolver) {
                    // console.log(resolver)
                    that.listContent(resolver)
                    that.loading = false;
                })

            },
            // 
            listContent(channel){
                const that = this;
                let datasObj = {};
                this.dataList = [];
                this.cloneDataList = [];

                // 
                this.totalPage = channel.data.total;
                this.currentPage = channel.data.current_page;
                this.pageSize = channel.data.per_page;
                // console.log(channel)
                channel.data.data.forEach(function (name) {
                    datasObj = {
                        id:name.id,
                        area : name.area,
                        project_name : name.project_name,
                        win_bid_unit : name.win_bid_unit,
                        win_bid_price : name.win_bid_price,
                        contract_sign_at : name.contract_sign_at,
                        work_days : name.work_days,
                        // begin_at : name.begin_at,
                        // end_at : name.end_at,
                        begin_end:(function () {
                            if (name.begin_at == null){
                                return ''
                            }else{
                                return name.begin_at+''+name.end_at
                            }
                        })(),
                        contract_date_remark: name.contract_date_remark,
                        project_index : name.project_index,
                        project_type : name.project_type,
                        project_manager : name.project_manager,
                        business_channel : name.business_channel,
                        partner : name.partner,
                        win_bid_publicity : (function () {
                            if (name.win_bid_publicity == null){
                                return ''
                            }else{
                                return name.win_bid_publicity
                            }
                        })(),
                        is_end : name.is_end,
                        is_up_to_par : name.is_up_to_par,
                        achievement_unit: name.achievement_unit,
                        remark : name.remark,
                        editting : false,
                        saving : false,

                    }
                    that.dataList.push(datasObj)
                    that.cloneDataList.push(datasObj)

                })
            },
            exportData (type) {

                if (type === 1) {
                    // console.log(this)
                    this.$refs.table.exportCsv({
                        filename: '',
                        columns:this.columnsList.filter((col, index) => index < 17)
                        /*data:this.cloneDataList.filter((data,index)=>{
                            // console.log(data)
                            return data;
                        })*/
                        // columns:this.columnsList.filter((col, index) => index < 17),
                        // data: this.cloneDataList.filter((data, index) => this.formatData(data))
                    });
                } else if (type === 2) {
                    this.$refs.table.exportCsv({
                        filename: 'Sorting and filtering data',
                        original: false
                    });
                } else if (type === 3) {
                    this.$refs.table.exportCsv({
                        filename: 'Custom data',
                        // columns: this.columns8.filter((col, index) => index < 4),
                        // data: this.data7.filter((data, index) => index < 4)
                    });
                }
            }
        }

    }
</script>

<style scoped lang="less">
    h1 {
        margin: 50px auto 30px;
    }
    #btn{
        margin: 20px;
    }

    .table_box{
        /*width: 100%;*/
        /*min-width: 1160px;*/
        /*max-width: 2010px;*/
        margin: 0 20px;
        /*height: 65vh;*/
        overflow: auto;
    }

    .ivu-table-wrapper{
        /*width: 2400px;*/
        margin: 0 auto;
        /*.ivu-table-cell{
            padding: 0!important;
        }*/
    }

    .logo{
        width: 80px;
        margin: 0 auto;
        position: absolute;
        top: 10px;
        left: 10px;
        img{
            width: 100%;
        }
    }

    .back{
        width: 50px;
        margin: 0 auto;
        position: absolute;
        top: 20px;
        right: 50px;
    }

</style>