var editButton = function (vm, h, currentRow, index) {
    return h('Button', {
        props: {
            size: 'small',
            type: currentRow.editting ? 'success' : 'primary',
            loading: currentRow.saving
        },
        style: {
            margin: '0 5px'
        },
        on: {
            click: function () {
                // console.log('');

                /*if (JSON.stringify(publicData) == "{}"){
                    publicData = Object.assign(publicData,currentRow)
                }*/
                // ,,render,https://cn.vuejs.org/v2/api/#render
                // handleBackdataedittingsaving
                // var tempData = vm.handleBackdata(currentRow)
                // var currentData = vm.handleBackdata(vm.dataList[index])
                if (!currentRow.editting) {
                    //
                    publicData = {};
                    publicData = Object.assign(publicData,currentRow)

                    //
                    var num = 1;
                    vm.dataList.forEach(function (name) {
                        if (name.editting){
                            num++
                        }
                    })
                    if(num>=2){
                        vm.$Message.warning('');
                        return false;
                    }

                    vm.loading = true;
                    vm.$Message.loading('');
                    setTimeout(function () {
                        currentRow.editting = true;
                        publicData.editting = true;

                        vm.loading = false;
                        vm.$Message.success('');
                    },300)



                } else {
                    // console.log(tempData,vm.dataList[index])
                    // ,,
                    /*if (JSON.stringify(tempData) == JSON.stringify(vm.dataList[index])) {
                        console.log('');
                        publicData.editting = false;
                        return currentRow.editting = false;
                    }*/
                    // vm.loading = true;
                    // vm.$Message.loading('');

                    // setTimeout(function () {
                        publicData.saving = true;
                        vm.saveData(currentRow, index,function () {
                            //
                            if (vm.success){
                                publicData = {};
                            }
                        })

                        // currentRow.saving = true;
                    // },300)
                }
            }
        }
    }, currentRow.editting ? '' : '');
};
