var selectButton = function (vm, h, currentRow, params,index) {
    // console.log(currentRow,params)
    
    return h('Select', {
        props: {
            placeholder:'',
            clearable:params.column.clearable?true:false,
            value: currentRow[params.column.key],
            transfer:true
        },
        style: {
            width:'100%',
            top: '-17px'
        },
        on: {
            'on-change'(value) {
                
                // setTimeout(()=>{
                    publicData[params.column.key] = value;

                    // currentRow = Object.assign(currentRow, publicData)

                    // currentRow[params.column.key] = value;

                // },10)

                setTimeout(function () {

                    var text = $('td.input_select .ivu-select-selection i');
                    var input = $('td.input_select input');
                    input.eq(0).val(text.eq(0).siblings('span').text());
                    input.eq(2).val(text.eq(1).siblings('span').text());
                    input.eq(4).val(text.eq(2).siblings('span').text());

                },10)



                // console.log($('td.input_select input'));
                // console.log($('td.input_select .ivu-select-selection i'));

            }
        }
    }, params.column.option.map(function (item) {
        return h('Option', {
            props: {
                value: item.value || item,
                label: item.label || item
            },
            style: {
                textAlign:'center'
            },
        }, item.label || item);
    }));
};

//  
var deleteButton = function (vm, h, currentRow, index) {
    return h('Poptip', {
            props: {
                confirm: true,
                title: currentRow.WRAPDATASTATUS != '' ? '?' : '?',
                transfer: true,
                placement: 'left'
            },
            on: {
                'on-ok': function () {
                    vm.deleteData(currentRow, index)
                }
            }
        },
        [
            h('Button', {
                style: {
                    color: '#8a8f86',
                    fontSize: '18px',
                    padding: '2px 7px 0',
                    border: 'none',
                    outline: 'none',
                    focus: {
                        '-webkit-box-shadow': 'none',
                        'box-shadow': 'none'
                    }
                },
                domProps: {
                    title: ''
                },
                props: {
                    size: 'small',
                    // type: 'ghost',
                    ghost:true,
                    // icon: 'android-delete',
                    icon: 'md-trash',
                    placement: 'left'
                }
            })
        ]);
};

export {
    publicData,
    // findObjectInOption,
    editButton,
    inputButton,
    selectButton,
    deleteButton
}

